import React, { Component, useState, useEffect } from 'react';
import './App.css';
import './nprogress.css';
import { EventList } from './EventList';
import { CitySearch } from './CitySearch';
import { NumberOfEvents } from './NumberOfEvents';
import EventGenre from './data-visualization/EventGenre';
import WelcomeScreen from './WelcomeScreen';
import { WarningAlert } from './Alert';

import { getEvents, extractLocations, checkToken, getAccessToken } from
  './api';

import logo from './assets/MeetAppLogo.png';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts";

export default function App() {

  let [events, setEvents] = useState([]),
    [locations, setLocations] = useState([]),
    [displayEvents, setDisplayEvents] = useState([]),
    [selectedLocation, setSelectedLocation] = useState('all'),
    [warningText, setWarningText] = useState(''),
    [numberDisplayed, setNumberDisplayed] = useState(32);

  const handleOnline = () => {
    !navigator.onLine
      ? setWarningText('You are currently using the app offline. Events may be out of date.')
      : setWarningText('');
  };

  useEffect(() => {
    handleOnline();
    getEvents().then((res) => {
      setEvents(res);
      setDisplayEvents(res);
      setLocations(extractLocations(res));
    });
  }, []);

  useEffect(() => {
    handleOnline();
    const locationEvents = (
      selectedLocation === 'all'
        ? events
        : events.filter((event) => event.location === selectedLocation)
    ).slice(0, numberDisplayed || 32);
    setDisplayEvents(locationEvents);
  }, [selectedLocation, numberDisplayed, events]);

  const getData = () => {
    // const { locations, events } = this.state;
    const data = locations.map(location => {
      const number = events.filter(event => event.location === location).length
      const city = location.split(', ').shift()
      return { city, number };
    })
    return data;
  };


  // if (showWelcomeScreen === undefined) return <div
  //   className="App" />

  return (
    <div className="App">

      <WarningAlert id="warning-text"
        text={warningText}
      />

      <Container fluid>
        <Row className="justify-content-md-center">

          <Col xs={12}>
            <img src={logo} alt='logo' className='logo-web' />
          </Col>
          <br />

          <Col xs={12} md={12}>
            <br />

            <h5 id="city-header">Check out upcoming Web Development events near you!!! :)</h5>

            <br />

            <CitySearch
              locations={locations}
              setSelectedLocation={setSelectedLocation}
            />

            <NumberOfEvents
              numberDisplayed={numberDisplayed}
              setNumberDisplayed={setNumberDisplayed}
            /><br />

          </Col>
        </Row>


        <div className="data-vis-wrapper">
          <ResponsiveContainer height={300} >
            <ScatterChart margin={{ top: 20, right: 20, bottom: 20, left: 20 }}>
              <CartesianGrid />
              <XAxis type="category" dataKey="city" name="city" />
              <YAxis
                allowDecimals={false}
                type="number"
                dataKey="number"
                name="number of events"
              />
              <Tooltip cursor={{ strokeDasharray: "3 3" }} />
              <Scatter data={getData()} fill="#8884d8" />
            </ScatterChart>
          </ResponsiveContainer>

          <EventGenre events={events} />
        </div>
        <br />

        <Row>
          <Col md={12}>
            <div className="eventList-container h-75 overflow-scroll">
              <EventList
                events={displayEvents} />
            </div>
          </Col>
        </Row>

      </Container>

      {/* <WelcomeScreen showWelcomeScreen={showWelcomeScreen}
        getAccessToken={() => { getAccessToken() }} /> */}

    </div >

  );
}


