import React, { Component, useState } from 'react';
import { ErrorAlert } from './Alert';

export function NumberOfEvents({ numberDisplayed, setNumberDisplayed }) {
  let [errorText, setErrorText] = useState('');
  const handleInputChange = (event) => {
    setErrorText('');
    const count = parseInt(event.target.value);
    if (count) {
      count < 1 || !Number.isInteger(count)
        ? setErrorText('Please enter a number between 1 and 32.')
        : setNumberDisplayed(count);
    } else if (count) {
      count > 32
        ? setErrorText('Please enter a number between 1 and 32.')
        : setNumberDisplayed(count);
    } else {
      setNumberDisplayed('');
    }
  };

  return (

    <div className="NumberOfEvents ">

      <div className="py-3 d-flex flex-row-reverse align-items-center">

        <input
          type="number"
          className="event-number-input rounded text-align-center"
          value={numberDisplayed}
          onChange={(e) => handleInputChange(e)} />
        <span className="pe-2">Events displayed: </span>
      </div>

      <div className="d-flex flex-row-reverse align-items-center">
        <ErrorAlert text={errorText} />
      </div>

    </div>

  );
}
