import React, { Component, useState, useEffect } from 'react';
import { InfoAlert } from './Alert';

import { InputGroup } from 'react-bootstrap';
import { FormControl } from 'react-bootstrap';

import cityImg from './assets/buildings.png';

export function CitySearch({ locations, setSelectedLocation }) {
  let [query, setQuery] = useState(''),
    [isVisible, setIsVisible] = useState(false),
    [infoText, setInfoText] = useState(''),
    [suggestions, setSuggestions] = useState([]);

  const handleInputChange = (userInput) => {
    const results = userInput
      ? locations.filter((location) =>
        location.toUpperCase().includes(userInput.toUpperCase())
      )
      : locations;

    results.length === 0
      ? setInfoText(
        'We cannot find the city you are looking for. Please try another city.'
      )
      : setInfoText('');

    setSuggestions(results);
    setQuery(userInput);
  };

  const handleItemClick = (suggestion) => {
    handleInputChange(suggestion);
    setIsVisible(false);
    setSelectedLocation(suggestion);
  };

  const handleOnBlur = (event) => {
    if (
      !event.relatedTarget ||
      event.relatedTarget.className !== 'matchSuggestion'
    ) {
      setIsVisible(false);
    }
  };

  useEffect(() => {
    setSuggestions(locations);
  }, [locations]);

  return (
    <div className="CitySearch">

      <InfoAlert id="info-alert" text={infoText} />

      <InputGroup className="mb-3 align-center">
        <InputGroup.Prepend>
          <img id="cityImg"
            alt=""
            src={cityImg}
            width="35"
            height="35"
            fluid="true"
          />
          {/* <EditLocationIcon /> */}
        </InputGroup.Prepend>
        <FormControl
          placeholder="Filter events by city"
          aria-label="Search for events by city"
          aria-describedby="basic-addon1"
          type="text"
          className="city"
          value={query}
          onChange={(e) => handleInputChange(e.target.value)}
          onFocus={() => setIsVisible(true)}
          onBlur={(e) => handleOnBlur(e)}

        />
      </InputGroup>

      <ul
        className="suggestions w-100 flex-column align-items-center"
        style={isVisible
          ? { display: 'flex' }
          : { display: 'none' }}>
        {suggestions.map((suggestion) => (
          <li
            className="matchSuggestion"
            key={suggestion}
            onClick={() => handleItemClick(suggestion)}
          >{suggestion}</li>
        ))}
        <li
          className="seeAll"
          key="all"
          onClick={() => handleItemClick('')}>
          <b>See all cities</b>
        </li>
      </ul>

    </div>
  );
}
